/////////// gap
$gp:0.5;$cg:1;
@while($cg < 10){
  .gap-#{$cg}{
    gap: $gp+rem;
  }
  @include query(xlg){
    .gap-xlg-#{$cg}{
      gap: $gp+rem;
    }
  }
  @include query(lg){
    .gap-lg-#{$cg}{
      gap: $gp+rem;
    }
  }
  @include query(md){
    .gap-md-#{$cg}{
      gap: $gp+rem;
    }
  }
  @include query(sm){
    .gap-sm-#{$cg}{
      gap: $gp+rem;
    }
  }
  $cg:$cg+1;
  $gp:$gp+0.5
}